html,body{width:100%;height:100%;font-family:'Poppins';margin:0;padding:0;box-sizing:border-box}*{font-family:inherit;box-sizing:inherit}#root{width:100%;height:100%}.wrapper{position:relative;overflow:hidden;height:100%;width:100%;background-color:#cfcfd6}.wrapper nav{display:flex;justify-content:space-between;align-items:center;height:80px;width:100%;padding:10px 20px}.wrapper nav i{font-size:18px;margin:0 10px}.wrapper nav .lang{margin-left:auto;font-size:12px;font-weight:600;text-transform:uppercase}.wrapper ul.social{position:absolute;bottom:260px;left:-150px;transform:rotateZ(-90deg);margin:0;padding:10px;list-style:none}.wrapper ul.social li{display:inline;margin-right:80px;font-size:10px;font-weight:600;text-transform:lowercase}.wrapper ul.q-nav{position:absolute;top:100px;right:-50px;margin:0;padding:10px;list-style:none}.wrapper ul.q-nav li{text-align:right;margin-right:80px;font-size:10px;font-weight:600;text-transform:lowercase}.wrapper ul.q-nav li:hover{text-decoration:underline}.wrapper .text-yellow{color:#d3ba4a}.wrapper .overlay{background:#d3ba4a;position:absolute;width:100%;height:100%;z-index:100}.wrapper .overlay.st{left:0}.wrapper .overlay.nd{left:33.3%}.wrapper .overlay.rd{left:66.6%}.wrapper .sponsor{font-size:12px;text-align:center;position:absolute;bottom:100px;right:100px;transform:rotate(180deg)}.wrapper .sponsor p{margin:0;padding:0}.wrapper .sponsor h4{margin:0;padding:0;font-size:20px;letter-spacing:-1px;transform:scaleY(-1);text-transform:uppercase;font-weight:900}.wrapper .text{position:absolute;transform:rotate(-90deg);left:100px;bottom:200px;overflow:visible}.wrapper .text h1,.wrapper .text h2,.wrapper .text h3,.wrapper .text p{margin:0;padding:0;line-height:1.25}.wrapper .text h1{font-size:120px;letter-spacing:-10px;width:600px;height:150px;font-weight:900;text-transform:uppercase;margin-bottom:25px;line-height:1.2;white-space:nowrap;position:relative;overflow:hidden}.wrapper .text h2{overflow:visible;white-space:nowrap;position:absolute;font-size:120px;font-weight:normal;letter-spacing:0px;font-weight:900;line-height:.75;z-index:-1;top:80px;left:-100px;color:#d3ba4a;text-transform:uppercase}.wrapper .text h3{font-size:40px;text-transform:uppercase;font-weight:600;line-height:1;width:600px}.wrapper .text p{width:200px;font-size:12px;margin-top:30px;font-weight:500;position:relative;overflow:hidden;height:110px}.wrapper #logo{position:absolute;top:50%;right:0;transform:translate(-50%, -50%);width:70vh;height:70vh}

