@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;900&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.13.0/css/all.min.css');


// RESET

html,
body {
  width: 100%;
  height: 100%;
  font-family: 'Poppins';
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* {
  font-family: inherit;
  box-sizing: inherit;
}

#root {
  width: 100%;
  height: 100%;
}

.wrapper {
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;

  background-color: #cfcfd6;

  //navbar 
  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    width: 100%;
    padding: 10px 20px;

    i {
      font-size: 18px;
      margin: 0 10px;
    }

    .lang {
      margin-left: auto;
      font-size: 12px;
      font-weight: 600;
      text-transform: uppercase;
    }




  }



  // social

  ul.social {
    position: absolute;
    bottom: 260px;
    left: -150px;
    transform: rotateZ(-90deg);
    margin: 0;
    padding: 10px;
    list-style: none;

    li {
      display: inline;
      margin-right: 80px;
      font-size: 10px;
      font-weight: 600;
      text-transform: lowercase;
    }
  }


  ul.q-nav {
    position: absolute;
    top: 100px;
    right: -50px;
    margin: 0;
    padding: 10px;
    list-style: none;

    li {
      &:hover {
        text-decoration: underline;
      }

      text-align: right;
      margin-right: 80px;
      font-size: 10px;
      font-weight: 600;
      text-transform: lowercase;
    }
  }

  .text-yellow {
    color: #d3ba4a;
  }

  .overlay {
    background: #d3ba4a;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 100;

    &.st {
      left: 0;
    }

    &.nd {
      left: 33.3%;
    }

    &.rd {
      left: 66.6%;
    }
  }


  // sponsor
  .sponsor {
    font-size: 12px;
    text-align: center;
    position: absolute;
    bottom: 100px;
    right: 100px;
    transform: rotate(180deg);

    p {
      margin: 0;
      padding: 0;
    }

    h4 {
      margin: 0;
      padding: 0;
      font-size: 20px;
      letter-spacing: -1px;
      transform: scaleY(-1);
      text-transform: uppercase;
      font-weight: 900;
    }
  }


  // text
  .text {
    position: absolute;
    transform: rotate(-90deg);

    left: 100px;
    bottom: 200px;
    overflow: visible;

    h1,
    h2,
    h3,
    p {
      margin: 0;
      padding: 0;
      line-height: 1.25;
    }

    h1 {
      font-size: 120px;
      letter-spacing: -10px;
      width: 600px;
      height: 150px;
      font-weight: 900;
      text-transform: uppercase;
      margin-bottom: 25px;
      line-height: 1.2;
      white-space: nowrap;
      position: relative;
      overflow: hidden;
    }

    h2 {
      overflow: visible;
      white-space: nowrap;
      position: absolute;
      font-size: 120px;
      font-weight: normal;
      letter-spacing: 0px;
      font-weight: 900;
      line-height: .75;
      z-index: -1;
      top: 80px;
      left: -100px;

      color: #d3ba4a;
      text-transform: uppercase;
    }

    h3 {
      font-size: 40px;
      text-transform: uppercase;
      font-weight: 600;
      line-height: 1;
      width: 600px;
    }

    p {
      width: 200px;
      font-size: 12px;
      margin-top: 30px;
      font-weight: 500;
      position: relative;
      overflow: hidden;
      height: 110px;
    }
  }


  // img
  #logo {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(-50%, -50%);
    width: 70vh;
    height: 70vh;
  }

}